import { Button } from "@/components/ui/button"
import { Plus } from 'lucide-react';
import {
    Dialog,
    DialogClose,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from "@/components/ui/dialog"
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { useState } from "react";
import { productApi, Product } from "@/hooks/products/useProductApi";
import { useAddProduct } from "@/hooks/products/useAddProducts";
import { useProductModal } from "@/contexts/productRegister-context";
interface HeadProps {
    onProductAdded: (product: Product) => void;
}
import { useCategories } from "@/hooks/global/fetching/useCategories";



export default function Head({ onProductAdded }: HeadProps) {
    const [name, setName] = useState("");
    const { open, setOpen, barcode, setBarcode } = useProductModal(); const [category_id, setCategoryId] = useState("");
    const [price, setPrice] = useState(0);
    const [quantity, setQuantity] = useState(0);
    const [showSuccess, setShowSuccess] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");
    const { addProduct, loading, error, success, reset } = useAddProduct();
    const { categories, loading: categoriesLoading, error: categoriesError } = useCategories();
    const [categorySearch, setCategorySearch] = useState("");

    const handleAddProduct = async () => {
        const newProduct = {
            name,
            barcode: barcode ?? "",
            category_id: Number(category_id),
            price: Number(price),
            quantity: Number(quantity),
        };
        // result should be the product returned from addProduct (with id)
        const result = await addProduct(newProduct);
        if (result && onProductAdded) {
            onProductAdded(result); // <-- Pass the product with id
            setName("");
            setBarcode("");
            setCategoryId("");
            setPrice(0);
            setQuantity(0);
            setOpen(false);
            setShowSuccess(true);
        } else {
            setOpen(false);
            setShowSuccess(true);
        }
    };

    return (
        <div className="flex justify-between items-center h-full">
            <div className="text-4xl font-bold">
                <h1>Products</h1>
            </div>
            <Dialog open={open} onOpenChange={setOpen}>
                <DialogTrigger asChild>
                    <Button
                        className="flex items-center justify-center gap-2 p-6"
                        onClick={() => {
                            setOpen(true);
                            setName("");
                            setBarcode("");
                            setCategoryId("");
                            setPrice(0);
                            setQuantity(0);
                        }}
                    >
                        <Plus className="text-white w-5 h-5 !w-5 !h-5" />
                        Add Product
                    </Button>
                </DialogTrigger>
                <DialogContent className='data-[state=open]:!zoom-in-100 data-[state=open]:slide-in-from-left-20 data-[state=open]:duration-600 sm:max-w-xl'>
                    <DialogHeader>
                        <DialogTitle>Product</DialogTitle>
                        <DialogDescription>Fill in the details below to add a new product.</DialogDescription>
                    </DialogHeader>
                    <div className='flex-col gap-4'>
                        <div className='flex flex-col gap-2'>
                            <Label htmlFor='name-1'>Product Name</Label>
                            <Input id='name-1' name='name' value={name} onChange={e => setName(e.target.value)} />
                        </div>
                        <div className='flex flex-row gap-2 mt-4'>
                            <div className="flex-[50%] flex flex-col gap-2">
                                <Label htmlFor='barcode-1'>Barcode</Label>
                                <Input id='barcode-1' name='barcode' value={barcode || ""} onChange={e => setBarcode(e.target.value)} />
                            </div>
                            <div className="flex-[50%] flex flex-col gap-2">
                                <Label htmlFor='category-1'>Category</Label>
                                <div>
                                    <DropdownMenu>
                                        <DropdownMenuTrigger asChild>
                                            <Input
                                                id="category-1"
                                                readOnly
                                                value={
                                                    categories.find(c => String(c.id) === String(category_id))?.name || ""
                                                }
                                                placeholder="Select category"
                                                className="cursor-pointer"
                                            />
                                        </DropdownMenuTrigger>
                                        <DropdownMenuContent className="w-64">
                                            <div className="p-2">
                                                <Input
                                                    placeholder="Search category..."
                                                    value={categorySearch}
                                                    onChange={e => setCategorySearch(e.target.value)}
                                                    className="mb-2"
                                                />
                                            </div>
                                            {categoriesLoading && (
                                                <div className="p-2 text-gray-400">Loading...</div>
                                            )}
                                            {categoriesError && (
                                                <div className="p-2 text-red-500">{categoriesError}</div>
                                            )}
                                            {!categoriesLoading && !categoriesError && (
                                                <>
                                                    {categories
                                                        .filter(cat =>
                                                            cat.name.toLowerCase().includes(categorySearch.toLowerCase())
                                                        )
                                                        .map(cat => (
                                                            <DropdownMenuItem
                                                                key={cat.id}
                                                                onClick={() => {
                                                                    setCategoryId(String(cat.id));
                                                                    setCategorySearch("");
                                                                }}
                                                            >
                                                                {cat.name}
                                                            </DropdownMenuItem>
                                                        ))}
                                                    {categories.filter(cat =>
                                                        cat.name.toLowerCase().includes(categorySearch.toLowerCase())
                                                    ).length === 0 && (
                                                            <div className="p-2 text-gray-400">No categories found.</div>
                                                        )}
                                                </>
                                            )}
                                        </DropdownMenuContent>
                                    </DropdownMenu>
                                </div>
                            </div>
                        </div>
                        <div className='flex flex-row gap-2 my-4'>
                            <div className="flex-[50%] flex flex-col gap-2">
                                <Label htmlFor='price-1'>Price</Label>
                                <Input
                                    id='price-1'
                                    name='price'
                                    type="number"
                                    value={price}
                                    onChange={e => setPrice(Number(e.target.value))}
                                />
                            </div>
                            <div className="flex-[50%] flex flex-col gap-2">
                                <Label htmlFor='quantity-1'>Quantity</Label>
                                <Input
                                    id='quantity-1'
                                    name='quantity'
                                    type="number"
                                    value={quantity}
                                    onChange={e => setQuantity(Number(e.target.value))}
                                />
                            </div>
                        </div>
                    </div>
                    <DialogFooter>
                        <DialogClose asChild>
                            <Button variant='outline'>Cancel</Button>
                        </DialogClose>
                        <Button onClick={handleAddProduct}>Add Product</Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>
            <Dialog
                open={showSuccess}
                onOpenChange={(open) => {
                    setShowSuccess(open);
                    if (!open) reset(); // Reset only after dialog closes
                }}
            >                <DialogContent>
                    <div className="flex flex-col items-center justify-center py-2">
                        <div className="mb-4">
                            {error ? (
                                <span className="text-red-500 text-4xl">&#10007;</span>
                            ) : (
                                <span className="text-green-500 text-4xl">&#10003;</span>
                            )}
                        </div>
                        <DialogTitle className="mb-2 text-center">
                            {error ? "Error Adding Product" : "Product Added!"}
                        </DialogTitle>
                        <DialogDescription className="mb-4 text-center">
                            {error
                                ? error
                                : "Congratulations! Your product has been added successfully."}
                        </DialogDescription>
                        {error ? (
                            <div className="flex gap-2 mt-3">
                                <Button
                                    variant="outline"
                                    onClick={() => {
                                        setShowSuccess(false); // Close error dialog
                                        setOpen(true);    // Reopen form dialog
                                        reset();              // Clear error state, keep inputs
                                    }}
                                >
                                    Back
                                </Button>
                                <Button
                                    onClick={() => {
                                        setShowSuccess(false);
                                    }}
                                >
                                    Close
                                </Button>
                            </div>
                        ) : (
                            <Button
                                onClick={() => {
                                    setShowSuccess(false);
                                }}
                            >
                                Close
                            </Button>
                        )}
                    </div>
                </DialogContent>
            </Dialog>
        </div>
    );
}